<template>
  <div class="container">
    <div class="row">
      <div class="col-4"></div>
      <div class="col-4">
        <input
          v-model="szukane"
          type="text"
          class="form-control"
          style="margin: 10px"
          placeholder="Wyszukaj Osoby"
        />
      </div>
      <div class="col-4"></div>
      <table class="table table-bordered table-striped table-dark">
        <thead>
          <tr>
            <th>Imie</th>
            <th>Nazwisko</th>
            <th>Numer Telefonu</th>
            <th>Edytowanie</th>
          </tr>
        </thead>
        <tbody>
          <WierszTabela
            v-for="k in listaOsob"
            :key="k.id"
            :daneosoba="k"
            @usun="$emit('usuniencie', k)"
            @zapisz="$emit('zapisywanie', k)"
          />
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
import WierszTabela from "../osoba/WierszOsob.vue";

export default {
  components: {
    WierszTabela,
  },
  data() {
    let szukane = "";
    return {
      szukane,
    };
  },
  methods: {
    walonefiltrowanie(obiekt) {
      return (
        obiekt.Imie.includes(this.szukane) ||
        obiekt.Nazwisko.includes(this.szukane) ||
        obiekt.Telefon.includes(this.szukane)
      );
    },
  },
  name: "TabelaOsob",
  props: ["listaOsob"],
};
</script>
