<template>
  <div class="container">
    <div class="row">
      <table class="table table-bordered table-striped table-dark">
        <thead>
          <tr>
            <th>Imie</th>
            <th>Nazwisko</th>
            <th>Wpłacona Kwota</th>
            <th>Data Wpłaty</th>
            <th></th>
          </tr>
        </thead>
        <tbody v-for="k in listaOsob" :key="k.id">
          <tr
            style="text-align: center"
            v-if="
              wplaty.filter(
                (e) => e.uczen_id == k.id && e.skladka_id == wplatySkladka.id
              ).length == 0
            "
          >
            <td>
              {{ k.Imie }}
            </td>

            <td>{{ k.Nazwisko }}</td>

            <td>
              <input
                v-if="idUczniaWplacajacego == k.id"
                v-model="kwotaDane"
                type="text"
                class="form-control"
                placeholder="Kwota Wpłaty"
              />
            </td>
            <td></td>
            <td>
              <button
                type="button"
                class="btn btn-sm btn-danger col-4"
                style="margin: 10px"
                v-on:click="
                  (idUczniaWplacajacego = k.id), (ukryjPrzycisk = true)
                "
                v-if="idUczniaWplacajacego != k.id && ukryjPrzycisk == false"
              >
                Dodaj
              </button>
              <div v-if="idUczniaWplacajacego == k.id">
                <button
                  type="button"
                  class="btn btn-sm btn-dark"
                  style="margin: 10px"
                  v-on:click="
                    $emit('zaplacil', k, wplatySkladka, kwotaDane),
                      (idUczniaWplacajacego = ''),
                      (kwotaDane = ''),
                      (ukryjPrzycisk = false)
                  "
                >
                  Zapisz
                </button>
                <button
                  type="button"
                  class="btn btn-sm btn-dark"
                  style="margin: 10px"
                  v-on:click="
                    (idUczniaWplacajacego = false),
                      (kwotaDane = ''),
                      (ukryjPrzycisk = false)
                  "
                >
                  Anuluj
                </button>
              </div>
            </td>
          </tr>
          <template
            v-if="
              wplaty.filter(
                (e) => e.uczen_id == k.id && e.skladka_id == wplatySkladka.id
              ).length > 0
            "
          >
            <tr
              style="text-align: center"
              v-for="wplata in wplaty.filter(
                (e) => e.uczen_id == k.id && e.skladka_id == wplatySkladka.id
              )"
              :key="wplata.Id"
            >
              <td>
                {{ k.Imie }}
              </td>

              <td>{{ k.Nazwisko }}</td>

              <td>
                <input
                  v-if="
                    idUczniaWplacajacego == k.id &&
                    ukryjPrzycisk == false &&
                    edytowanie == true
                  "
                  v-model="kwotaDane"
                  type="text"
                  class="form-control"
                  placeholder="Kwota Wpłaty"
                />
                <div v-else>{{ wplata.kwota | currencyFormat }}</div>
              </td>
              <td>{{ wplata.data }}</td>
              <td>
                <button
                  v-if="idUczniaWplacajacego != k.id && ukryjPrzycisk == false"
                  type="button"
                  class="btn btn-sm btn-success"
                  v-on:click="
                    (idUczniaWplacajacego = k.id), (ukryjPrzycisk = true)
                  "
                >
                  Usuń
                </button>
                <button
                  style="margin-left: 10px"
                  v-if="idUczniaWplacajacego != k.id && ukryjPrzycisk == false"
                  type="button"
                  class="btn btn-sm btn-success"
                  v-on:click="
                    (edytowanie = true),
                      (idUczniaWplacajacego = k.id),
                      (kwotaDane = wplata.kwota)
                  "
                >
                  Edytuj
                </button>

                <div
                  v-if="
                    idUczniaWplacajacego == k.id &&
                    ukryjPrzycisk == true &&
                    edytowanie == false
                  "
                >
                  <button
                    type="button"
                    class="btn btn-sm btn-dark"
                    style="margin: 10px"
                    v-on:click="
                      $emit('edytowanie', wplata, kwotaDane),
                        (idUczniaWplacajacego = ''),
                        (kwotaDane = ''),
                        (ukryjPrzycisk = false)
                    "
                  >
                    Zapisz
                  </button>
                  <button
                    type="button"
                    class="btn btn-sm btn-dark"
                    style="margin: 10px"
                    v-on:click="
                      (idUczniaWplacajacego = ''), (ukryjPrzycisk = false)
                    "
                  >
                    Anuluj
                  </button>
                </div>
                <div v-if="idUczniaWplacajacego == k.id && edytowanie == true">
                  <div>Usunąć?</div>
                  <button
                    type="button"
                    class="btn btn-sm btn-dark"
                    style="margin: 10px"
                    v-on:click="
                      $emit('usun', wplata),
                        (idUczniaWplacajacego = ''),
                        (ukryjPrzycisk = false),
                        (edytowanie = false)
                    "
                  >
                    Tak
                  </button>
                  <button
                    type="button"
                    class="btn btn-sm btn-dark"
                    style="margin: 10px"
                    v-on:click="
                      (idUczniaWplacajacego = ''),
                        (ukryjPrzycisk = false),
                        (edytowanie = false)
                    "
                  >
                    Nie
                  </button>
                </div>
              </td>
            </tr>
          </template>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    let kwotaDane = "";
    let wpisKwoty = false;
    let szukane = "";
    let ukryjPrzycisk = false;
    let edytowanie = false;
    return {
      szukane,
      idUczniaWplacajacego: wpisKwoty,
      kwotaDane,
      ukryjPrzycisk,
      edytowanie,
    };
  },
  name: "TabelaWplat",
  props: ["listaOsob", "wplaty", "wplatySkladka"],
};
</script>
